import { useEffect } from "react";
import {
  DivForm,
  ContainerTickets,
  AllTickets,
  HomeSearchTop,
  ContainerTicketsTitle,
} from "./styled";
import Logo from "../../../assets/logo-horizontal.png";
import { useState } from "react";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import { CardViagens } from "../../CardViagens";
import { Button, SelectPicker } from "rsuite";
import { DatePicker, Stack } from "rsuite";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import { travelService } from "../../../services/travelService";
import "rsuite/dist/rsuite.min.css";

export const Tickets = () => {
  const location = useLocation();
  const [travel, setTravel] = useState();
  const [viagens, setViagens] = useState([]);
  const [cities, setCities] = useState([]);
  const [origin, setOrigin] = useState("");
  const [destiny, setDestiny] = useState("");
  const [loading, setLoading] = useState(true);
  const [notTravel, setNotTravel] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    const { origin, destiny, date, cities } = location.state;
    setCities(cities);
    if (origin && destiny && date) {
      travelService
        .search({
          origin: origin,
          destiny: destiny,
          dateOfTravel: date,
        })
        .then((response) => {
          setOrigin(origin);
          setDestiny(destiny);
          setViagens(response?.data?.data || []);
          if (response.data?.data.length > 0) {
            setLoading(false);
            localStorage.setItem(
              "originName",
              response.data.data[0].nameOrigin
            );
            localStorage.setItem(
              "destinyName",
              response.data.data[0].nameDestiny
            );
          } else {
            setNotTravel(true);
            setLoading(false);
            setViagens([]);
          }
        })
        .catch((err) => {
          setLoading(false);
          console.log(err);
        });
    }
  }, [location.state]);

  return (
    <div>
      <HomeSearchTop>
        <img
          src={Logo}
          style={{ cursor: "pointer" }}
          width="200px"
          alt="description"
          onClick={() => {
            navigate("/");
          }}
        />
        <DivForm>
          <form onSubmit={{}}>
            <FormControl variant="filled" sx={{ m: 1, minWidth: 120 }}>
              <InputLabel id="demo-simple-select-filled-label">
                Destino
              </InputLabel>
              <SelectPicker
                data={cities}
                style={{ width: 224 }}
                defaultValue={origin}
                onSelect={(e) => {
                  setTravel({ ...travel, destiny: e });
                }}
              />
            </FormControl>
            <FormControl variant="filled" sx={{ m: 1, minWidth: 120 }}>
              <InputLabel id="demo-simple-select-filled-label">
                Origem
              </InputLabel>
              <SelectPicker
                data={cities}
                style={{ width: 224 }}
                defaultValue={destiny}
                onSelect={(e) => {
                  setTravel({ ...travel, destiny: e });
                }}
              />
            </FormControl>
            <FormControl variant="filled" sx={{ m: 1, minWidth: 120 }}>
              <Stack
                direction="column"
                alignItems="flex-start"
                spacing={6}
                style={{ marginLeft: 5 }}
              >
                <DatePicker
                  id="data"
                  size="lg"
                  format="dd/MM/yyyy"
                  placeholder="Data da Viagem"
                />
              </Stack>
            </FormControl>
            <FormControl variant="filled" sx={{ m: 1, width: 200 }}>
              <Button
                variant="contained"
                color="primary"
                sx={{
                  backgroundColor: "#ff5722",
                  color: "white",
                  "&:hover": {
                    backgroundColor: "#e64a19",
                  },
                }}
                type="submit"
              >
                Busca passagem
              </Button>
            </FormControl>
          </form>
        </DivForm>
      </HomeSearchTop>

      <ContainerTickets loading={viagens?.length === 0 || !viagens}>
        <ContainerTicketsTitle>
          {viagens && viagens.length > 0 && (
            <>
              <span>
                Passagens de ônibus de{" "}
                <strong>{localStorage.getItem("originName")}, CE </strong> para{" "}
                <strong>{localStorage.getItem("destinyName")}, CE</strong>
              </span>
            </>
          )}
        </ContainerTicketsTitle>

        <AllTickets>
          {!loading && viagens.length > 0
            ? viagens?.map((viag) => {
                return <CardViagens viagens={viag} />;
              })
            : !notTravel && (
                <div>
                  <h1>Estamos Buscando as melhores viagens para você...</h1>
                </div>
              )}

          {notTravel && (
            <div>
              <h1>Nao encontramos viagens para este periodo...</h1>
            </div>
          )}
        </AllTickets>
      </ContainerTickets>
    </div>
  );
};
