import * as React from "react";
import { useEffect } from "react";
import { Button, ButtonToolbar, SelectPicker } from "rsuite";
import LocationIcon from "@rsuite/icons/Location";

import {
  HomeSearchInto,
  HomeSearchWrapper,
  DivForm,
  HomeSearchTop,
  Form,
  TitleHomeSeach,
} from "./styled";
import { useNavigate } from "react-router-dom";
import Logo from "../../assets/logo-horizontal.png";
import { DatePicker, Stack } from "rsuite";
import InputLabel from "@mui/material/InputLabel";
import FormControl from "@mui/material/FormControl";
import { useState } from "react";
import { cityService } from "../../services/cityService";
import "rsuite/dist/rsuite.min.css";

const HomeSearch = (Tenta) => {
  const [isDateAnterior, setIsDataAnterior] = React.useState(new Date());
  const navigate = useNavigate();
  const [travel, setTravel] = useState();

  const [cities, setCities] = useState([]);
  const onSubmit = (e) => {
    e.preventDefault();
    if (isDateAnterior) {
      alert("Data anterior a data atual");
    }
    console.log("=======>", travel);
    navigate("/travel", {
      state: { ...travel, cities },
    });
  };

  //verifica se a data é anterior a data de hoje
  function verificaData(date) {
    if (date) {
      const data = new Date(date)
        .toLocaleDateString("pt-BR", {
          day: "2-digit",
          month: "2-digit",
          year: "numeric",
        })
        .split("/")
        .reverse()
        .join("-");
      console.log("data", data);
      const dataAtual = new Date();
      if (data < dataAtual) {
        setIsDataAnterior(true);
      } else {
        setIsDataAnterior(false);
        setTravel({ ...travel, date: data.replace(/\//g, "-") });
      }
    }
  }

  useEffect(() => {
    cityService.getAll().then((response) => {
      const data = response.data.map((item) => ({
        label: item.name,
        value: item.id,
      }));
      setCities(data);
    });
  }, []);

  return (
    <div>
      <HomeSearchTop>
        <img
          src={Logo}
          style={{ cursor: "pointer" }}
          width="200px"
          alt="description"
          onClick={() => {
            navigate("/");
          }}
        />

        <ButtonToolbar>
          <label>Entrar em Contato</label>
          <Button appearance="ghost">Cadastre-se</Button>

          <Button appearance="ghost">Entrar</Button>
        </ButtonToolbar>
      </HomeSearchTop>
      <HomeSearchWrapper>
        <HomeSearchInto>
          <TitleHomeSeach>
            <h3>Encontre sua próxima viagem</h3>
          </TitleHomeSeach>

          <Form onSubmit={onSubmit}>
            <DivForm>
              <div>
                <SelectPicker
                  data={cities}
                  onSelect={(e) => {
                    setTravel({ ...travel, origin: e });
                  }}
                  size="lg"
                  placeholder={
                    <>
                      <LocationIcon
                        style={{
                          color: "#7303fc",
                          marginTop: "-2px",
                          marginRight: "5px",
                        }}
                      />
                      De onde você vai?
                    </>
                  }
                  style={{ width: 224 }}
                />
              </div>

              <SelectPicker
                data={cities}
                size="lg"
                style={{ width: 224, color: "black" }}
                placeholder={
                  <>
                    <LocationIcon
                      style={{
                        color: "#7303fc",
                        marginTop: "-2px",
                        marginRight: "5px",
                      }}
                    />
                    Para onde você vai?
                  </>
                }
                onSelect={(e) => {
                  setTravel({ ...travel, destiny: e });
                }}
              />
              <DatePicker
                format="dd/MM/yyyy"
                placeholder="Quando você vai?"
                ranges={[]}
                style={{ width: 260 }}
                onChange={verificaData}
                size="lg"
              />
              <Button
                variant="contained"
                color="primary"
                sx={{
                  backgroundColor: "#ff5722",
                  padding: "10px",
                  color: "white",
                  "&:hover": {
                    backgroundColor: "#e64a19",
                  },
                }}
                size="lg"
                type="submit"
              >
                <strong>Encontrar meu próximo destino</strong>
              </Button>
            </DivForm>
          </Form>
        </HomeSearchInto>
      </HomeSearchWrapper>
    </div>
  );
};

export default HomeSearch;
